import React from "react";
import { ConfigProvider, Layout, Typography } from "antd";

import "./App.css";
import ComingSoon from "./Common/CominSoon";
import Navigation from "./Common/Navigation";

interface IState {
	isDarkEnabled: boolean;
}

class App extends React.Component<{}, IState> {
	state = {
		isDarkEnabled: true,
	};

	changeTheme = () => {
		this.setState(
			(prevState) => ({
				isDarkEnabled: !prevState.isDarkEnabled,
			}),
			() =>
				ConfigProvider.config({
					theme: {
						primaryColor: this.state.isDarkEnabled ? "#1890ff" : "#00ff00",
					},
				})
		);
	};

	render(): React.ReactNode {
		const { Header, Footer, Content } = Layout;
		const { Text, Link } = Typography;
		const currentYear = new Date().getFullYear();

		return (
			<div
				style={{
					backgroundColor: this.state.isDarkEnabled ? "darkgray" : "lightgray",
				}}
			>
				<Layout>
					<Header style={{ backgroundColor: "white" }}>
						<Navigation />
					</Header>
					<Layout>
						<Content>
							<ComingSoon />
						</Content>
					</Layout>
					<Footer>
						<Text>
							&copy;{" "}
							<strong>
								<Link href="https://www.farmegrow.com/" target="_blank">
									Farm E-grow Imex Private Ltd.
								</Link>
							</strong>
							, {currentYear}
						</Text>
					</Footer>
				</Layout>
			</div>
		);
	}
}

export default App;
