import { Typography, Skeleton } from "antd";

function ComingSoon() {
	const { Title } = Typography;
	return (
		<div className="center">
			<div>
				<Title>Coming Soon...</Title>
				<Skeleton.Button
					active={true}
					size="large"
					shape="round"
					block={true}
				/>
			</div>
		</div>
	);
}

export default ComingSoon;
